import React, { FC, useCallback, useState } from "react";

import { Icon, IconName } from "../../Icon/Icon";
import Typography from "../../Typography/Typography";

import {
	LeftBorder,
	LeftBorderTriangle,
	RightBorder,
	StyledGlobalNavbarItemProps,
	StyledGlobalNavLink,
	TooltipContent,
	TooltipItem,
	TooltipWrapper,
} from "./GlobalNavbarItem.styles";

export interface GlobalNavbarItemProps extends StyledGlobalNavbarItemProps {
	dataQA: string
	ariaLabel?: string
	menuIcon: IconName
	tooltipContent?: string
	tooltipHeader?: string
	className?: string
}

/** @deprecated Use GlobalNavigationItem */
const GlobalNavbarItem: FC<GlobalNavbarItemProps> = ({
	dataQA,
	ariaLabel,
	menuIcon,
	isActive,
	tooltipContent,
	tooltipHeader,
	tooltipZIndex,
	accentColor,
	accentBackgroundImage,
	className,
	m,
	mt,
	mb,
	mr,
	ml,
}) => {
	const [isVisible, setIsVisible] = useState(false);

	const handleMouseOver = useCallback(() => setIsVisible(true), []);
	const handleMouseOut = useCallback(() => setIsVisible(false), []);

	const renderedTooltip = isVisible && (
		<TooltipItem
			tooltipZIndex={tooltipZIndex}
			aria-expanded
			data-qa={`${dataQA}-tooltip-wrapper`}
		>
			<LeftBorderTriangle data-qa={`${dataQA}-tooltip-triangle`} accentColor={accentColor} />
			<LeftBorder
				data-qa={`${dataQA}-tooltip-border`}
				accentColor={accentColor}
				accentBackgroundImage={accentBackgroundImage}
			/>
			<Typography
				dataQA={`${dataQA}-tooltip-header`}
				variant="displayMedium"
				lineHeight="normal"
			>
				{tooltipHeader}
			</Typography>
			<TooltipContent data-qa={`${dataQA}-tooltip-content`}>{tooltipContent}</TooltipContent>
		</TooltipItem>
	);

	return (
		<TooltipWrapper
			onMouseEnter={handleMouseOver}
			onMouseLeave={handleMouseOut}
		>
			<StyledGlobalNavLink
				data-qa={dataQA}
				aria-label={ariaLabel}
				isActive={isActive}
				accentColor={accentColor}
				accentBackgroundImage={accentBackgroundImage}
				className={className}
				m={m}
				mt={mt}
				mb={mb}
				mr={mr}
				ml={ml}
			>
				<Icon data-qa={`${dataQA}-menu-icon`} name={menuIcon} />
				<RightBorder data-qa={`${dataQA}-border`} />
			</StyledGlobalNavLink>
			{ renderedTooltip }
		</TooltipWrapper>
	);
};

GlobalNavbarItem.displayName = "GlobalNavbarItem";
export default GlobalNavbarItem;
